<template>
  <div>
  <el-dialog ref='term-detail' title="标注数据" :visible.sync='visible' :size="size" @close='close'>
    <el-tabs type="card" @tab-click="tabClick" value="addAnno" >
      <el-tab-pane label="添加标签" name="addAnno">
        <el-form :model="annotation">
          <el-form-item label="词条内容" size="mini" style="text-align:center">
            <span style="font-size:18px">{{annotation.term}}</span>
          </el-form-item>
          <el-form-item label="词条分类" style="text-align:center">
            <el-select v-model="annotation.type" placeholder="请选择" size="large" filterable>
              <el-option
                v-for="item in annotation.types"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                <span style="float: left">{{item.label}}</span>
                <span style="float:right;color:#8492a6; font-size: 13px">{{item.value}}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="同时加入原子词库" style="text-align:center">
            <el-switch
              v-model="annotation.isNewTerm"
              on-color="#13ce66"
              off-color="#ff4949"
              >
            </el-switch>
          </el-form-item>
        </el-form>
        <el-row>
          <el-col :span="24" style="text-align:center">
            <el-button type="primary" @click="mySubmit">标注词条</el-button>
            <el-button @click='visible=false'>关闭</el-button>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="更新标签" name='combineAnno'  >
        <el-form :model="annotation" label-position="left" label-width="110px">
          <el-form-item label="新原子词">
            <span style="font-size:18px">{{annotation.term}}</span>
          </el-form-item>
          <el-form-item label="类型集合">
            <el-select v-model="annotation.type" 
              filterable allow-create size="large" 
              placeholder="请选择">
              <el-option
                v-for="item in annotation.types"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              <span style="float: left">{{item.label}}</span>
              <span style="float:right;color:#8492a6; font-size: 13px">{{item.value}}</span>
              </el-option>
            </el-select>
          </el-form-item>       
        </el-form>
        <el-row style="text-align:center">
          <el-col :span="24">
            <el-button type="primary" @click="combineAtomicTermConfirm">更新标签</el-button>
            <el-button @click='visible=false'>关闭</el-button>
          </el-col>
        </el-row>          
      </el-tab-pane>
    </el-tabs>
  </el-dialog>
  </div>
</template>
<script>
    import {selectAnnotationByTermTypeArr,getTypes} from '@/api/type'
    import {addCombineAtomicTerm,updateMultipleAtomicAnnotation} from '@/api/annotation'
    export default {
      props: ['annotation', 'submit'],
      data () {
        return {
          visible: false,
          a:'hello Janine',
          loading:false,
          annotationArr:[],
          size:'tiny',
          bratConfig:null,
          showName:'',
          multipleSelection:[],
          combineAtomicTerm:{
            type:null,
            types:[]
          }
        };
      },
      created() {
          var self=this
          
      } ,
      methods: {
        queryType(){
          var self=this;
          getTypes()
          .then((response)=>{
            var serverTypes=response.data
            var i=0,leni=serverTypes.length
              for(;i<leni;i++){
                if(!serverTypes[i].typeCode.endsWith('-unconfirmed')){
                  self.combineAtomicTerm.types.push({
                    typeName: serverTypes[i].typeName,
                    typeCode: serverTypes[i].typeCode
                  })
                }
              }
          })
        },
        combineAtomicTermConfirm(){
          var self=this

          updateMultipleAtomicAnnotation(self.annotation.bratId,self.annotation.combineAnno,self.annotation.term,self.annotation.type)
          .then((response)=>{
            if(response.succ==true){
              self.mySubmit();
            }else{
              self.$message.error(response.msg);
            }
          })
        },
        close(){
          var self=this
          self.annotationArr=[]
        },
        open () {
          this.visible = true
        },
        mySubmit: function(){
          this.visible = false
          this.submit()
        },
        tabClick(tab,event){
          var self=this
          self.size='tiny'
          console.log(self.annotation)
        }
      },
      components:{

      }
    }
</script>


